import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
  return (
    <div className='cards'>
      <h1>Check Out My Personal Projects</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <CardItem
              src='images/img-9.jpg'
              text='Explore My Stock Calls Website '
              label='Adventure'
              path='/services'
            />
            <CardItem
              src='images/img-10.jpg'
              text='Coming Soon'
              label='TBD'
              path='/services'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='images/img-10.jpg'
              text='Coming Soon'
              label='TBD'
              path='/services'
            />
            <CardItem
              src='images/img-10.jpg'
              text='Coming Soon'
              label='TBD'
              path='/products'
            />
            <CardItem
              src='images/img-10.jpg'             
               text='Coming Soon'
              label='TBD'
              path='/sign-up'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
